<template>
  <div class="about">
    <video ref="videoRef" autoplay></video>
    <canvas ref="canvasRef"></canvas>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import * as faceapi from 'face-api.js';
let videoRef = ref();
let canvasRef = ref();

onMounted(async () => {
  await faceapi.nets.ssdMobilenetv1.loadFromUri('/models');
  await faceapi.nets.faceLandmark68Net.loadFromUri('/models');
  await faceapi.nets.faceExpressionNet.loadFromUri('/models');
  await faceapi.nets.tinyFaceDetector.loadFromUri('/models');
  await faceapi.nets.faceRecognitionNet.loadFromUri('/models');
  let stream = await navigator.mediaDevices.getUserMedia({
    video: {
      facingMode: 'user',
      width: { ideal: 1280 },
      height: { ideal: 720 },
    },
  })
  videoRef.value.srcObject = stream;
  startFaceDetection();
});
let startFaceDetection = () => {
  const video = videoRef.value;
  const canvas = canvasRef.value;

  video.addEventListener('play', () => {
    console.log(video.videoWidth, video.videoHeight, video.clientWidth, video.clientHeight);
    // 
    let ratio = video.videoWidth / video.videoHeight;  // 0;
    video.style.height = video.clientWidth / ratio;
    canvas.style.height = video.clientWidth / ratio;

    const displaySize = { width: video.clientWidth, height: video.clientHeight };
    faceapi.matchDimensions(canvas, displaySize); // 

    setInterval(async () => {
      const detections = await faceapi.detectAllFaces(video, new faceapi.SsdMobilenetv1Options())
        .withFaceLandmarks()
        .withFaceExpressions();

      const resizedDetections = faceapi.resizeResults(detections, displaySize);
      canvas.getContext('2d').clearRect(0, 0, canvas.width, canvas.height);
      faceapi.draw.drawDetections(canvas, resizedDetections); // 
      faceapi.draw.drawFaceLandmarks(canvas, resizedDetections); // 
      faceapi.draw.drawFaceExpressions(canvas, resizedDetections); // 
    }, 100);
  });
}
</script>
<style lang="scss">
.about {
  width: 100%;
  height: 100%;
  position: relative;

  video {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 100%;
  }

  canvas {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 100%;
  }
}
</style>