<template>
    <div class="box">
        <video ref="videoRef" autoplay></video>
        <canvas ref="canvasRef"></canvas>
    </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import * as faceapi from 'face-api.js'; // face-api
let videoRef = ref();
let canvasRef = ref();
// 
let getUserMedia = (success, error) => {
    const constraints = {
        video: {
            facingMode: 'user',
            width: { ideal: 1280 },
            height: { ideal: 720 },
        },
    };
    navigator.mediaDevices
        .getUserMedia(constraints)
        .then(success)
        .catch(error);
}
// // 
// let drawRect = () => {
//     const context = canvasRef.value.getContext('2d');
//     context.clearRect(0, 0, canvasRef.value.width, canvasRef.value.height);
//     faceapi.detectSingleFace(videoRef.value).withFaceLandmarks().withFaceExpressions()
//         .then((detections) => {
//             let box = detections.alignedRect.box;
//             context.rect(box.x, box.y, box.width, box.height);
//             context.stroke();
//         })
//         .catch((err) => {
//             console.log(err, 12);
//         })
//     requestAnimationFrame(drawRect, context)
// }
onMounted(async () => {
    await faceapi.nets.ssdMobilenetv1.loadFromUri('/models');
    await faceapi.nets.faceLandmark68Net.loadFromUri('/models');
    await faceapi.nets.faceExpressionNet.loadFromUri('/models');
    await faceapi.nets.tinyFaceDetector.loadFromUri('/models');
    await faceapi.nets.faceRecognitionNet.loadFromUri('/models');
    getUserMedia(streams => {
        //
        if (videoRef.value) videoRef.value.srcObject = streams;
        videoRef.value.addEventListener('loadedmetadata', function () {
            canvasRef.value.width = videoRef.value.videoWidth;
            canvasRef.value.height = videoRef.value.videoHeight;
            const context = canvasRef.value.getContext('2d');
            setInterval(async () => {
                context.clearRect(0, 0, canvasRef.value.width, canvasRef.value.height);
                faceapi.detectAllFaces(videoRef.value).withFaceLandmarks().withFaceExpressions()
                    .then((detections) => {
                        detections.forEach((item) => {
                            let box = item.alignedRect.box;
                            // console.log(item);
                            context.lineWidth = '5';
                            context.strokeStyle = 'blue';
                            context.beginPath();
                            context.rect(box.x, box.y, box.width, box.height);
                            context.closePath();
                            context.stroke();
                        })
                    })
                    .catch((err) => {
                        console.log(err, 12);
                    })
                // faceapi.detectSingleFace(videoRef.value).withFaceLandmarks().withFaceExpressions()
                //     .then((detections) => {
                //         let box = detections.alignedRect.box;
                //         context.beginPath();
                //         context.rect(box.x, box.y, box.width, box.height);
                //         context.closePath();
                //         context.stroke();
                //     })
                //     .catch((err) => {
                //         console.log(err, 12);
                //     })
            })
            // drawRect();
            // faceapi.detectSingleFace(videoRef.value).withFaceLandmarks().withFaceExpressions()
            //     .then((detections) => {
            //         let box = detections.alignedRect.box;
            //         context.rect(box.x, box.y, box.width, box.height);
            //         context.stroke();
            //     })
            //     .catch((err) => {
            //         console.log(err, 12);
            //     })
        })
    },
        err => console.log(err))
})

</script>

<style lang="scss" scoped>
.box {
    height: 100%;
    position: relative;

    video {
        width: 100%;
        display: block;
    }

    canvas {
        width: 100%;
        height: 400px;
        position: absolute;
        top: 0;
        display: block;
    }
}
</style>
